/**
 * ========================================
 * MAIN.JS - BOMBAS AYALA
 * Coordinador principal y punto de entrada
 * ========================================
 */

class BombasAyalaApp {
    constructor() {
        // Estado de la aplicacin
        this.isInitialized = false;
        this.currentPage = this.detectCurrentPage();
        this.controllers = {};
        this.config = {
            phone: '51997984102',
            email: 'info@bombasayala.com',
            address: 'Lima, Per',
            analytics: {
                enabled: true,
                trackingId: 'GA_MEASUREMENT_ID' // Reemplazar con tu ID real
            }
        };

        this.init();
    }

    init() {
        console.log(' Iniciando Bombas Ayala App...');
        
        // Verificar dependencias
        if (!this.checkDependencies()) {
            console.error(' Dependencias no encontradas');
            return;
        }

        // Inicializar componentes globales
        this.initGlobalComponents();
        
        // Inicializar componentes especficos de pgina
        this.initPageSpecificComponents();
        
        // Configurar eventos globales
        this.setupGlobalEvents();
        
        // Finalizar inicializacin
        this.finishInitialization();
    }

    // ==================
    // VERIFICACIN DE DEPENDENCIAS
    // ==================
    checkDependencies() {
        const required = ['BombasAyalaUtils'];
        
        for (const dep of required) {
            if (typeof window[dep] === 'undefined') {
                console.error(` Dependencia requerida no encontrada: ${dep}`);
                return false;
            }
        }
        
        console.log(' Todas las dependencias estn disponibles');
        return true;
    }

    // ==================
    // DETECCIN DE PGINA
    // ==================
    detectCurrentPage() {
        const path = window.location.pathname.toLowerCase();
        const body = document.body;
        
        // Detectar por pathname
        if (path.includes('inicio') || path === '/' || path.includes('index')) {
            return 'inicio';
        } else if (path.includes('productos')) {
            return 'productos';
        } else if (path.includes('servicios')) {
            return 'servicios';
        } else if (path.includes('galeria') || path.includes('proyectos') || path.includes('fotos')) {
            return 'galeria';
        } else if (path.includes('nosotros') || path.includes('about')) {
            return 'nosotros';
        } else if (path.includes('contacto') || path.includes('contact')) {
            return 'contacto';
        } else if (path.includes('soporte') || path.includes('faq') || path.includes('ayuda')) {
            return 'soporte';
        }
        
        // Detectar por clases del body o elementos especficos
        if (body.classList.contains('page-inicio') || document.querySelector('#heroBackground')) {
            return 'inicio';
        } else if (body.classList.contains('page-productos') || document.querySelector('.producto-card')) {
            return 'productos';
        } else if (body.classList.contains('page-servicios') || document.querySelector('.servicio-card')) {
            return 'servicios';
        } else if (body.classList.contains('page-galeria') || document.querySelector('.tarjeta-proyecto')) {
            return 'galeria';
        } else if (body.classList.contains('page-nosotros') || document.querySelector('.numero-estadistica')) {
            return 'nosotros';
        } else if (body.classList.contains('page-contacto') || document.querySelector('#formularioContacto')) {
            return 'contacto';
        } else if (body.classList.contains('page-soporte') || document.querySelector('.faq-item')) {
            return 'soporte';
        }
        
        // Pgina genrica si no se detecta ninguna especfica
        return 'general';
    }

    // ==================
    // COMPONENTES GLOBALES
    // ==================
    initGlobalComponents() {
        console.log(' Inicializando componentes globales...');
        
        // Header (siempre presente)
        if (typeof HeaderController !== 'undefined') {
            try {
                this.controllers.header = new HeaderController();
                console.log(' Header inicializado');
            } catch (e) {
                console.warn(' Error inicializando Header:', e.message);
            }
        }
        
        // Flotantes (siempre presente)
        if (typeof FlotantesController !== 'undefined') {
            try {
                this.controllers.flotantes = new FlotantesController();
                console.log(' Flotantes inicializados');
            } catch (e) {
                console.warn(' Error inicializando Flotantes:', e.message);
            }
        }
    }

    // ==================
    // COMPONENTES ESPECFICOS DE PGINA
    // ==================
    initPageSpecificComponents() {
        console.log(` Inicializando componentes para pgina: ${this.currentPage}`);
        
        switch (this.currentPage) {
            case 'inicio':
                this.initInicioComponents();
                break;
            case 'productos':
                this.initProductosComponents();
                break;
            case 'servicios':
                this.initServiciosComponents();
                break;
            case 'galeria':
                this.initGaleriaComponents();
                break;
            case 'nosotros':
                this.initNosotrosComponents();
                break;
            case 'contacto':
                this.initContactoComponents();
                break;
            case 'soporte':
                this.initSoporteComponents();
                break;
            default:
                console.log(' Pgina genrica - solo componentes globales');
        }
    }

    initInicioComponents() {
        if (typeof InicioController !== 'undefined') {
            try {
                this.controllers.inicio = new InicioController();
                console.log(' Inicio inicializado');
            } catch (e) {
                console.warn(' Error inicializando Inicio:', e.message);
            }
        }
    }

    initProductosComponents() {
        if (typeof ProductosController !== 'undefined') {
            try {
                this.controllers.productos = new ProductosController();
                console.log(' Productos inicializado');
            } catch (e) {
                console.warn(' Error inicializando Productos:', e.message);
            }
        }
    }

    initServiciosComponents() {
        if (typeof ServiciosController !== 'undefined') {
            try {
                this.controllers.servicios = new ServiciosController();
                console.log(' Servicios inicializado');
            } catch (e) {
                console.warn(' Error inicializando Servicios:', e.message);
            }
        }
    }

    initGaleriaComponents() {
        if (typeof GaleriaController !== 'undefined') {
            try {
                this.controllers.galeria = new GaleriaController();
                console.log(' Galera inicializada');
            } catch (e) {
                console.warn(' Error inicializando Galera:', e.message);
            }
        }
    }

    initNosotrosComponents() {
        if (typeof NosotrosController !== 'undefined') {
            try {
                this.controllers.nosotros = new NosotrosController();
                console.log(' Nosotros inicializado');
            } catch (e) {
                console.warn(' Error inicializando Nosotros:', e.message);
            }
        }
    }

    initContactoComponents() {
        if (typeof ContactoController !== 'undefined') {
            try {
                this.controllers.contacto = new ContactoController();
                console.log(' Contacto inicializado');
            } catch (e) {
                console.warn(' Error inicializando Contacto:', e.message);
            }
        }
    }

    initSoporteComponents() {
        if (typeof SoporteController !== 'undefined') {
            try {
                this.controllers.soporte = new SoporteController();
                console.log(' Soporte inicializado');
            } catch (e) {
                console.warn(' Error inicializando Soporte:', e.message);
            }
        }
    }

    // ==================
    // EVENTOS GLOBALES
    // ==================
    setupGlobalEvents() {
        // Manejo de errores globales
        window.addEventListener('error', this.handleGlobalError.bind(this));
        
        // Manejo de promesas rechazadas
        window.addEventListener('unhandledrejection', this.handleUnhandledRejection.bind(this));
        
        // Eventos de navegacin
        window.addEventListener('beforeunload', this.handleBeforeUnload.bind(this));
        
        // Eventos de rendimiento
        window.addEventListener('load', this.handlePageLoad.bind(this));
        
        // Eventos de red
        window.addEventListener('online', this.handleOnline.bind(this));
        window.addEventListener('offline', this.handleOffline.bind(this));
        
        console.log(' Eventos globales configurados');
    }

    handleGlobalError(event) {
        console.error(' Error global:', event.error);
        
        // Tracking de errores (opcional)
        if (this.config.analytics.enabled) {
            BombasAyalaUtils.trackEvent('javascript_error', 'error', event.error.message);
        }
    }

    handleUnhandledRejection(event) {
        console.error(' Promesa rechazada:', event.reason);
        
        // Tracking de errores (opcional)
        if (this.config.analytics.enabled) {
            BombasAyalaUtils.trackEvent('promise_rejection', 'error', event.reason.toString());
        }
    }

    handleBeforeUnload(event) {
        // Limpiar recursos si es necesario
        console.log(' Limpiando recursos antes de salir...');
    }

    handlePageLoad(event) {
        const loadTime = performance.now();
        console.log(` Pgina cargada en ${Math.round(loadTime)}ms`);
        
        // Tracking de rendimiento
        if (this.config.analytics.enabled) {
            BombasAyalaUtils.trackEvent('page_load_time', 'performance', this.currentPage, Math.round(loadTime));
        }
    }

    handleOnline(event) {
        console.log(' Conexin restaurada');
        BombasAyalaUtils.showToast('Conexin restaurada', 'success');
    }

    handleOffline(event) {
        console.log(' Sin conexin a internet');
        BombasAyalaUtils.showToast('Sin conexin a internet', 'warning');
    }

    // ==================
    // FINALIZACIN
    // ==================
    finishInitialization() {
        this.isInitialized = true;
        
        // Ocultar cualquier loader
        this.hideLoaders();
        
        // Mostrar contenido
        this.showContent();
        
        // Tracking de inicializacin
        if (this.config.analytics.enabled) {
            BombasAyalaUtils.trackEvent('app_initialized', 'system', this.currentPage);
        }
        
        console.log(' Bombas Ayala App inicializada correctamente');
        console.log(' Estado:', this.getAppState());
    }

    hideLoaders() {
        const loaders = document.querySelectorAll('.loader, .loading, .preloader');
        loaders.forEach(loader => {
            loader.style.opacity = '0';
            setTimeout(() => {
                if (loader.parentNode) {
                    loader.parentNode.removeChild(loader);
                }
            }, 300);
        });
    }

    showContent() {
        document.body.style.opacity = '1';
        document.body.classList.add('loaded');
    }

    // ==================
    // API PBLICA
    // ==================
    getAppState() {
        return {
            initialized: this.isInitialized,
            currentPage: this.currentPage,
            controllers: Object.keys(this.controllers),
            isMobile: BombasAyalaUtils.isMobile(),
            config: this.config
        };
    }

    getController(name) {
        return this.controllers[name] || null;
    }

    reload() {
        window.location.reload();
    }

    // Mtodos de conveniencia para acciones comunes
    openWhatsApp(message) {
        BombasAyalaUtils.openWhatsApp(message || 'Hola! Me interesa conocer ms sobre sus servicios.', this.config.phone);
    }

    callPhone() {
        window.location.href = `tel:+${this.config.phone}`;
    }

    sendEmail(subject = 'Consulta desde el sitio web') {
        const body = encodeURIComponent('Estimados,\n\nMe interesa conocer ms informacin sobre sus servicios.\n\nSaludos cordiales.');
        window.location.href = `mailto:${this.config.email}?subject=${encodeURIComponent(subject)}&body=${body}`;
    }
}

// ==================
// INICIALIZACIN AUTOMTICA
// ==================
function initBombasAyalaApp() {
    // Verificar que estemos en el browser
    if (typeof window === 'undefined') {
        console.warn(' No estamos en un entorno de navegador');
        return;
    }

    // Crear instancia global
    window.BombasAyala = new BombasAyalaApp();
    
    // API global simplificada
    window.BA = {
        app: window.BombasAyala,
        utils: window.BombasAyalaUtils,
        whatsapp: (msg) => window.BombasAyala.openWhatsApp(msg),
        call: () => window.BombasAyala.callPhone(),
        email: (subject) => window.BombasAyala.sendEmail(subject),
        state: () => window.BombasAyala.getAppState(),
        reload: () => window.BombasAyala.reload()
    };
    
    console.log(' API global disponible como window.BA');
}

// Auto-inicializar cuando el DOM est listo
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', initBombasAyalaApp);
} else {
    initBombasAyalaApp();
}

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = BombasAyalaApp;
}