/**
 * ========================================
 * CONTACTO.JS - BOMBAS AYALA
 * Formulario de contacto y mapa interactivo
 * ========================================
 */

class ContactoController {
    constructor() {
        this.formularioContacto = document.getElementById('formularioContacto');
        this.botonEnviar = document.querySelector('.boton-enviar');
        this.mapaInteractivo = document.getElementById('mapaInteractivo');
        this.informacionMapa = document.getElementById('informacionMapa');
        
        this.init();
    }

    init() {
        this.initFormulario();
        this.initMapa();
        this.initAnimaciones();
        this.initCopyToClipboard();
        
        console.log(' Contacto Controller inicializado');
    }

    // ==================
    // FORMULARIO DE CONTACTO
    // ==================
    initFormulario() {
        if (!this.formularioContacto) return;

        const inputs = this.formularioContacto.querySelectorAll('input, select, textarea');

        // Validacin en tiempo real
        inputs.forEach(input => {
            input.addEventListener('blur', () => {
                BombasAyalaUtils.validateField(input);
            });

            input.addEventListener('input', () => {
                if (input.classList.contains('error')) {
                    BombasAyalaUtils.validateField(input);
                }
            });
        });

        // Submit del formulario
        this.formularioContacto.addEventListener('submit', (e) => {
            e.preventDefault();
            this.enviarFormulario();
        });
    }

    enviarFormulario() {
        // Validar formulario completo
        if (!BombasAyalaUtils.validateForm(this.formularioContacto)) {
            this.mostrarMensaje('Por favor, corrige los errores en el formulario.', 'error');
            
            // Hacer scroll al primer error
            const primerError = this.formularioContacto.querySelector('.error');
            if (primerError) {
                primerError.focus();
                primerError.scrollIntoView({ behavior: 'smooth', block: 'center' });
            }
            return;
        }

        // Cambiar estado del botn
        const textoOriginal = this.botonEnviar.innerHTML;
        this.botonEnviar.innerHTML = '<i class="fas fa-spinner fa-spin"></i> ENVIANDO...';
        this.botonEnviar.disabled = true;

        // Obtener datos del formulario
        const formData = new FormData(this.formularioContacto);
        const datos = Object.fromEntries(formData);

        // Simular envo
        setTimeout(() => {
            console.log(' Datos del formulario:', datos);
            
            // Crear mensaje para WhatsApp
            const mensaje = BombasAyalaUtils.createQuoteMessage(datos);
            
            // Abrir WhatsApp
            BombasAyalaUtils.openWhatsApp(mensaje);
            
            // Mostrar mensaje de xito
            this.mostrarMensaje('Mensaje enviado correctamente! Te hemos redirigido a WhatsApp.', 'exito');
            
            // Limpiar formulario
            this.formularioContacto.reset();
            const inputs = this.formularioContacto.querySelectorAll('input, select, textarea');
            inputs.forEach(input => {
                input.classList.remove('error', 'success');
                BombasAyalaUtils.removeFieldError(input);
            });
            
            // Restaurar botn
            this.botonEnviar.innerHTML = textoOriginal;
            this.botonEnviar.disabled = false;
            
            // Tracking
            BombasAyalaUtils.trackEvent('form_submit', 'contacto', 'formulario_principal');
            
        }, 2000);
    }

    mostrarMensaje(mensaje, tipo) {
        // Remover mensaje anterior
        const mensajeAnterior = document.querySelector('.mensaje-formulario');
        if (mensajeAnterior) {
            mensajeAnterior.remove();
        }

        // Crear nuevo mensaje
        const divMensaje = document.createElement('div');
        divMensaje.className = `mensaje-formulario ${tipo}`;
        divMensaje.innerHTML = `
            <i class="fas ${tipo === 'exito' ? 'fa-check-circle' : 'fa-exclamation-triangle'}"></i>
            <span>${mensaje}</span>
        `;

        // Insertar mensaje
        this.formularioContacto.insertBefore(divMensaje, this.formularioContacto.firstChild);

        // Auto-remover despus de 5 segundos
        setTimeout(() => {
            if (divMensaje && document.body.contains(divMensaje)) {
                divMensaje.style.opacity = '0';
                setTimeout(() => {
                    if (document.body.contains(divMensaje)) {
                        divMensaje.remove();
                    }
                }, 300);
            }
        }, 5000);
    }

    // ==================
    // MAPA INTERACTIVO
    // ==================
    initMapa() {
        if (!this.mapaInteractivo || !this.informacionMapa) return;

        // Coordenadas de Bombas Ayala
        const coordenadas = {
            lat: -12.113154,
            lng: -77.050290
        };

        // Efectos hover
        this.mapaInteractivo.addEventListener('mouseenter', () => {
            this.informacionMapa.style.opacity = '0';
            this.informacionMapa.style.transform = 'translateY(-10px)';
        });

        this.mapaInteractivo.addEventListener('mouseleave', () => {
            this.informacionMapa.style.opacity = '1';
            this.informacionMapa.style.transform = 'translateY(0)';
        });

        // Click en el mapa
        this.mapaInteractivo.addEventListener('click', () => {
            const googleMapsUrl = `https://www.google.com/maps?q=${coordenadas.lat},${coordenadas.lng}`;
            window.open(googleMapsUrl, '_blank');
            
            BombasAyalaUtils.trackEvent('map_click', 'contacto', 'google_maps');
        });

        // Preloader para iframe
        const iframe = this.mapaInteractivo.querySelector('iframe');
        if (iframe) {
            iframe.addEventListener('load', () => {
                this.mapaInteractivo.classList.add('mapa-cargado');
            });
        }
    }

    // ==================
    // ANIMACIONES
    // ==================
    initAnimaciones() {
        // Animar elementos al hacer scroll
        BombasAyalaUtils.animateOnScroll('.seccion-formulario-contacto, .informacion-contacto-directo, .seccion-horarios');

        // Animacin adicional al hacer scroll
        window.addEventListener('scroll', () => {
            const seccionContacto = document.querySelector('.seccion-contacto');
            if (seccionContacto) {
                const rect = seccionContacto.getBoundingClientRect();
                
                if (rect.top < window.innerHeight && rect.bottom > 0) {
                    seccionContacto.classList.add('visible');
                }
            }
        });
    }

    // ==================
    // COPIAR AL CLIPBOARD
    // ==================
    initCopyToClipboard() {
        const elementosCopiables = document.querySelectorAll('.detalles-contacto span');
        
        elementosCopiables.forEach(span => {
            span.addEventListener('click', async () => {
                const texto = span.textContent;
                
                try {
                    await BombasAyalaUtils.copyToClipboard(texto);
                    
                    // Feedback visual
                    const original = span.textContent;
                    const originalColor = span.style.color;
                    
                    span.textContent = 'Copiado!';
                    span.style.color = '#27AE60';
                    
                    setTimeout(() => {
                        span.textContent = original;
                        span.style.color = originalColor;
                    }, 1000);
                    
                    // Toast notification
                    BombasAyalaUtils.showToast(`Copiado: ${texto}`, 'success');
                    
                    // Tracking
                    BombasAyalaUtils.trackEvent('copy_contact', 'contacto', texto);
                    
                } catch (err) {
                    console.warn('No se pudo copiar el texto:', err);
                    BombasAyalaUtils.showToast('No se pudo copiar', 'error');
                }
            });
        });
    }

    // ==================
    // MTODOS PBLICOS
    // ==================
    scrollToForm() {
        if (this.formularioContacto) {
            this.formularioContacto.scrollIntoView({
                behavior: 'smooth',
                block: 'start'
            });
        }
    }

    resetForm() {
        if (this.formularioContacto) {
            this.formularioContacto.reset();
            const inputs = this.formularioContacto.querySelectorAll('input, select, textarea');
            inputs.forEach(input => {
                input.classList.remove('error', 'success');
                BombasAyalaUtils.removeFieldError(input);
            });
        }
    }
}

// ==================
// FUNCIONES ADICIONALES
// ==================

// Inicializar mapa personalizado (funcin legacy)
function inicializarMapaPersonalizado() {
    const mapaInteractivo = document.getElementById('mapaInteractivo');
    const informacionMapa = document.getElementById('informacionMapa');
    
    if (mapaInteractivo && informacionMapa) {
        mapaInteractivo.addEventListener('click', () => {
            console.log(' Mapa clickeado');
        });
    }
}

// ==================
// INICIALIZACIN
// ==================
function initContacto() {
    window.contactoController = new ContactoController();
    
    // API global
    window.Contacto = {
        scrollToForm: () => window.contactoController?.scrollToForm(),
        resetForm: () => window.contactoController?.resetForm()
    };
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', () => {
    initContacto();
    inicializarMapaPersonalizado(); // Mantener compatibilidad
});

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = ContactoController;
}