/**
 * ========================================
 * GALERIA.JS - BOMBAS AYALA
 * Galera de proyectos con filtros y lightbox
 * ========================================
 */

class GaleriaController {
    constructor() {
        this.filtroBtns = document.querySelectorAll('.filtro-btn');
        this.tarjetasProyecto = document.querySelectorAll('.tarjeta-proyecto');
        this.btnVerMas = document.getElementById('btnVerMas');
        this.imagenesGaleria = document.querySelectorAll('.imagen-item img');
        
        // Estado
        this.filtroActual = 'todos';
        this.mostrandoTodos = false;
        this.proyectosPorPagina = 6;
        
        this.init();
    }

    init() {
        if (this.tarjetasProyecto.length === 0) {
            console.warn('Galera: No se encontraron proyectos');
            return;
        }

        this.setupFilters();
        this.setupVerMasButton();
        this.setupLightbox();
        this.setupAnimations();
        
        console.log(' Galera Controller inicializado');
    }

    // ==================
    // SISTEMA DE FILTROS
    // ==================
    setupFilters() {
        if (this.filtroBtns.length === 0) return;

        this.filtroBtns.forEach(btn => {
            btn.addEventListener('click', () => {
                this.cambiarFiltro(btn);
            });
        });

        // Aplicar filtro inicial
        this.actualizarBotonVerMas('todos');
    }

    cambiarFiltro(btnClickeado) {
        // Actualizar filtro activo
        this.filtroBtns.forEach(b => b.classList.remove('activo'));
        btnClickeado.classList.add('activo');
        
        const filtro = btnClickeado.dataset.filtro;
        this.filtroActual = filtro;
        
        // Resetear estado de "Ver ms"
        this.mostrandoTodos = false;
        
        // Aplicar filtro con animacin
        this.aplicarFiltroConAnimacion(filtro);
        
        // Actualizar botn "Ver ms"
        this.actualizarBotonVerMas(filtro);
        
        // Tracking
        BombasAyalaUtils.trackEvent('gallery_filter', 'proyectos', filtro);
        
        console.log(` Filtro galera aplicado: ${filtro}`);
    }

    aplicarFiltroConAnimacion(filtro) {
        this.tarjetasProyecto.forEach((tarjeta, index) => {
            const categoria = tarjeta.dataset.categoria;
            const esVisible = filtro === 'todos' || categoria === filtro;
            const esOculto = tarjeta.classList.contains('proyecto-oculto');
            
            // Animacin de salida
            tarjeta.style.transform = 'scale(0.8)';
            tarjeta.style.opacity = '0';
            
            setTimeout(() => {
                if (esVisible && (!esOculto || this.mostrandoTodos)) {
                    this.mostrarProyecto(tarjeta, index);
                } else {
                    this.ocultarProyecto(tarjeta);
                }
            }, 150);
        });
    }

    mostrarProyecto(tarjeta, index = 0) {
        tarjeta.style.display = 'block';
        
        // Animacin de entrada escalonada
        setTimeout(() => {
            tarjeta.style.transform = 'scale(1)';
            tarjeta.style.opacity = '1';
            tarjeta.style.transition = 'all 0.4s ease';
        }, index * 50);
    }

    ocultarProyecto(tarjeta) {
        tarjeta.style.display = 'none';
        tarjeta.style.transform = 'scale(0.8)';
        tarjeta.style.opacity = '0';
    }

    // ==================
    // BOTN "VER MS"
    // ==================
    setupVerMasButton() {
        if (!this.btnVerMas) return;

        this.btnVerMas.addEventListener('click', () => {
            this.toggleVerMasProyectos();
        });
    }

    toggleVerMasProyectos() {
        if (!this.mostrandoTodos) {
            this.mostrarMasProyectos();
        } else {
            this.mostrarMenosProyectos();
        }
    }

    mostrarMasProyectos() {
        this.btnVerMas.innerHTML = '<i class="fas fa-spinner fa-spin"></i> Cargando proyectos...';
        
        setTimeout(() => {
            const proyectosOcultos = this.getProyectosOcultos();
            
            proyectosOcultos.forEach((proyecto, index) => {
                this.mostrarProyecto(proyecto, index);
                proyecto.classList.remove('proyecto-oculto');
            });
            
            this.mostrandoTodos = true;
            this.actualizarBotonVerMas(this.filtroActual);
            
            // Tracking
            BombasAyalaUtils.trackEvent('ver_mas_proyectos', 'galeria', this.filtroActual);
            
        }, 800);
    }

    mostrarMenosProyectos() {
        // Ocultar proyectos adicionales
        this.tarjetasProyecto.forEach(proyecto => {
            if (!proyecto.classList.contains('proyecto-inicial')) {
                proyecto.classList.add('proyecto-oculto');
                this.ocultarProyecto(proyecto);
            }
        });
        
        this.mostrandoTodos = false;
        this.aplicarFiltroConAnimacion(this.filtroActual);
        this.actualizarBotonVerMas(this.filtroActual);
        
        // Tracking
        BombasAyalaUtils.trackEvent('ver_menos_proyectos', 'galeria', this.filtroActual);
    }

    getProyectosOcultos() {
        return Array.from(this.tarjetasProyecto).filter(proyecto => {
            const categoriaProyecto = proyecto.getAttribute('data-categoria');
            const esVisible = this.filtroActual === 'todos' || categoriaProyecto === this.filtroActual;
            const esOculto = proyecto.classList.contains('proyecto-oculto');
            
            return esVisible && esOculto;
        });
    }

    actualizarBotonVerMas(categoria = 'todos') {
        if (!this.btnVerMas) return;

        const proyectosOcultos = this.getProyectosOcultos();
        
        if (proyectosOcultos.length > 0 && !this.mostrandoTodos) {
            this.btnVerMas.style.display = 'inline-block';
            this.btnVerMas.innerHTML = '<i class="fas fa-plus-circle"></i> Ver Ms Proyectos';
        } else if (this.mostrandoTodos && proyectosOcultos.length === 0) {
            this.btnVerMas.innerHTML = '<i class="fas fa-minus-circle"></i> Ver Menos';
            this.btnVerMas.style.display = 'inline-block';
        } else {
            this.btnVerMas.style.display = 'none';
        }
    }

    // ==================
    // LIGHTBOX PARA IMGENES
    // ==================
    setupLightbox() {
        this.imagenesGaleria.forEach(img => {
            img.addEventListener('click', () => {
                this.abrirLightbox(img);
            });
        });
    }

    abrirLightbox(imagen) {
        // Crear modal
        const modal = document.createElement('div');
        modal.className = 'lightbox-modal';
        modal.style.cssText = `
            position: fixed;
            top: 0;
            left: 0;
            width: 100vw;
            height: 100vh;
            background: rgba(15, 35, 70, 0.95);
            display: flex;
            justify-content: center;
            align-items: center;
            z-index: 9999;
            cursor: zoom-out;
            opacity: 0;
            transition: opacity 0.3s ease;
        `;

        // Imagen ampliada
        const imagenAmpliada = new Image();
        imagenAmpliada.src = imagen.src;
        imagenAmpliada.style.cssText = `
            max-height: 90vh;
            max-width: 90vw;
            object-fit: contain;
            border-radius: 8px;
            box-shadow: 0 20px 40px rgba(0,0,0,0.3);
            transform: scale(0.8);
            transition: transform 0.3s ease;
        `;

        // Botn de cerrar
        const closeBtn = document.createElement('span');
        closeBtn.innerHTML = '&times;';
        closeBtn.style.cssText = `
            position: absolute;
            top: 30px;
            right: 30px;
            color: white;
            font-size: 40px;
            cursor: pointer;
            background: rgba(0,0,0,0.5);
            width: 60px;
            height: 60px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            transition: background 0.3s ease;
        `;

        closeBtn.addEventListener('mouseenter', () => {
            closeBtn.style.background = 'rgba(0,0,0,0.8)';
        });

        closeBtn.addEventListener('mouseleave', () => {
            closeBtn.style.background = 'rgba(0,0,0,0.5)';
        });

        // Informacin del proyecto (opcional)
        const infoContainer = this.crearInfoProyecto(imagen);

        modal.appendChild(imagenAmpliada);
        modal.appendChild(closeBtn);
        if (infoContainer) {
            modal.appendChild(infoContainer);
        }

        // Funcin para cerrar
        const cerrarModal = () => {
            modal.style.opacity = '0';
            imagenAmpliada.style.transform = 'scale(0.8)';
            
            setTimeout(() => {
                if (document.body.contains(modal)) {
                    document.body.removeChild(modal);
                }
                document.body.style.overflow = '';
            }, 300);
        };

        // Event listeners
        modal.addEventListener('click', (e) => {
            if (e.target === modal) cerrarModal();
        });
        
        closeBtn.addEventListener('click', cerrarModal);
        
        // Cerrar con Escape
        const handleEscape = (e) => {
            if (e.key === 'Escape') {
                cerrarModal();
                document.removeEventListener('keydown', handleEscape);
            }
        };
        document.addEventListener('keydown', handleEscape);

        // Agregar al DOM
        document.body.appendChild(modal);
        document.body.style.overflow = 'hidden';

        // Animar entrada
        setTimeout(() => {
            modal.style.opacity = '1';
            imagenAmpliada.style.transform = 'scale(1)';
        }, 50);

        // Tracking
        BombasAyalaUtils.trackEvent('lightbox_open', 'galeria', 'imagen_proyecto');
    }

    crearInfoProyecto(imagen) {
        const tarjeta = imagen.closest('.tarjeta-proyecto');
        if (!tarjeta) return null;

        const titulo = tarjeta.querySelector('.proyecto-titulo')?.textContent;
        const descripcion = tarjeta.querySelector('.proyecto-descripcion')?.textContent;
        
        if (!titulo) return null;

        const infoContainer = document.createElement('div');
        infoContainer.style.cssText = `
            position: absolute;
            bottom: 30px;
            left: 30px;
            right: 30px;
            background: rgba(0,0,0,0.8);
            color: white;
            padding: 20px;
            border-radius: 8px;
            max-width: 500px;
        `;

        infoContainer.innerHTML = `
            <h3 style="margin: 0 0 10px 0; font-size: 1.5rem;">${titulo}</h3>
            ${descripcion ? `<p style="margin: 0; opacity: 0.9;">${descripcion}</p>` : ''}
        `;

        return infoContainer;
    }

    // ==================
    // ANIMACIONES
    // ==================
    setupAnimations() {
        // Animar proyectos al hacer scroll
        BombasAyalaUtils.animateOnScroll('.tarjeta-proyecto, .paso-proceso, .titulo-seccion');
        
        // Animacin especfica para la galera
        const observer = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.opacity = '1';
                    entry.target.style.transform = 'translateY(0) scale(1)';
                }
            });
        });

        this.tarjetasProyecto.forEach(tarjeta => {
            tarjeta.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
            tarjeta.style.opacity = '0';
            tarjeta.style.transform = 'translateY(30px) scale(0.95)';
            observer.observe(tarjeta);
        });
    }

    // ==================
    // API PBLICA
    // ==================
    filtrarPor(categoria) {
        const filtro = document.querySelector(`[data-filtro="${categoria}"]`);
        if (filtro) {
            this.cambiarFiltro(filtro);
        }
    }

    getStats() {
        return {
            totalProyectos: this.tarjetasProyecto.length,
            proyectosVisibles: Array.from(this.tarjetasProyecto).filter(p => p.style.display !== 'none').length,
            filtroActual: this.filtroActual,
            mostrandoTodos: this.mostrandoTodos
        };
    }
}

// ==================
// INICIALIZACIN
// ==================
function initGaleria() {
    // Solo inicializar en pginas con galera
    if (document.querySelector('.tarjeta-proyecto, .galeria-proyectos')) {
        window.galeriaController = new GaleriaController();
        
        // API global
        window.Galeria = {
            filtrar: (categoria) => window.galeriaController?.filtrarPor(categoria),
            stats: () => window.galeriaController?.getStats()
        };
    }
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', initGaleria);

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = GaleriaController;
}