/**
 * ========================================
 * FLOTANTES.JS - BOMBAS AYALA
 * Elementos flotantes y funcionalidad WhatsApp
 * ========================================
 */

class FlotantesController {
    constructor() {
        this.flotantesContainer = document.querySelector('.flotantes');
        this.flotantes = document.querySelectorAll('.flotante');
        
        // Estado
        this.isVisible = true;
        this.lastScrollTop = 0;
        this.hideThreshold = 200;
        
        this.init();
    }

    init() {
        if (!this.flotantesContainer || this.flotantes.length === 0) {
            console.warn('Flotantes: Elementos no encontrados');
            return;
        }

        this.setupScrollBehavior();
        this.setupClickEffects();
        this.setupTooltips();
        this.setupWhatsAppIntegration();
        this.adjustForMobile();
        
        console.log(' Flotantes Controller inicializado');
    }

    // ==================
    // COMPORTAMIENTO SCROLL
    // ==================
    setupScrollBehavior() {
        window.addEventListener('scroll', BombasAyalaUtils.debounce(() => {
            const currentScroll = window.pageYOffset;
            const scrollDirection = currentScroll > this.lastScrollTop ? 'down' : 'up';
            
            // Mostrar/ocultar segn scroll
            if (currentScroll > this.hideThreshold) {
                if (scrollDirection === 'down') {
                    this.hide();
                } else if (scrollDirection === 'up') {
                    this.show();
                }
            } else {
                this.show();
            }
            
            this.lastScrollTop = currentScroll;
        }, 100), { passive: true });
    }

    // ==================
    // EFECTOS DE CLICK
    // ==================
    setupClickEffects() {
        this.flotantes.forEach((flotante) => {
            flotante.addEventListener('click', (e) => {
                this.addClickEffect(flotante);
                this.trackClick(flotante);
            });
        });
    }

    addClickEffect(flotante) {
        // Efecto visual de click
        flotante.style.transform = 'scale(0.9)';
        
        setTimeout(() => {
            flotante.style.transform = '';
        }, 150);
        
        // Vibracin en mviles
        if ('vibrate' in navigator) {
            navigator.vibrate(50);
        }
    }

    trackClick(flotante) {
        let tipo = 'unknown';
        
        if (flotante.classList.contains('whatsapp')) {
            tipo = 'WhatsApp';
        } else if (flotante.classList.contains('llamada') || flotante.classList.contains('phone')) {
            tipo = 'Llamada';
        } else if (flotante.classList.contains('instagram')) {
            tipo = 'Instagram';
        }
        
        console.log(` Click en flotante: ${tipo}`);
        
        // Tracking
        BombasAyalaUtils.trackEvent('flotante_click', 'contact', tipo);
    }

    // ==================
    // TOOLTIPS
    // ==================
    setupTooltips() {
        const tooltips = [
            'Chatea por WhatsApp',
            'Llmanos ahora',
            'Sguenos en Instagram'
        ];
        
        this.flotantes.forEach((flotante, index) => {
            if (index < tooltips.length) {
                flotante.setAttribute('data-tooltip', tooltips[index]);
                flotante.setAttribute('aria-label', tooltips[index]);
            }
        });
    }

    // ==================
    // INTEGRACIN WHATSAPP
    // ==================
    setupWhatsAppIntegration() {
        const whatsappBtn = document.querySelector('.flotante.whatsapp');
        
        if (whatsappBtn) {
            whatsappBtn.addEventListener('click', (e) => {
                e.preventDefault();
                
                const mensaje = this.createDefaultWhatsAppMessage();
                BombasAyalaUtils.openWhatsApp(mensaje);
                
                console.log(' WhatsApp abierto desde flotante');
            });
        }

        // Otros botones de WhatsApp en la pgina
        this.setupOtherWhatsAppButtons();
    }

    setupOtherWhatsAppButtons() {
        const whatsappButtons = document.querySelectorAll('[data-whatsapp], .boton-whatsapp');
        
        whatsappButtons.forEach(button => {
            button.addEventListener('click', (e) => {
                e.preventDefault();
                
                const customMessage = button.getAttribute('data-message');
                const producto = button.getAttribute('data-producto');
                const specs = button.getAttribute('data-especificaciones');
                
                let mensaje;
                
                if (customMessage) {
                    mensaje = customMessage;
                } else if (producto) {
                    mensaje = this.createProductWhatsAppMessage(producto, specs);
                } else {
                    mensaje = this.createDefaultWhatsAppMessage();
                }
                
                BombasAyalaUtils.openWhatsApp(mensaje);
                
                console.log(' WhatsApp abierto:', { producto, specs });
            });
        });
    }

    createDefaultWhatsAppMessage() {
        return 'Hola! Me interesa conocer ms sobre sus servicios de bombas de agua. \n\nPodran brindarme informacin sobre sus productos y servicios?';
    }

    createProductWhatsAppMessage(producto, specs = '') {
        let mensaje = `Hola! Me interesa el siguiente producto:\n\n`;
        mensaje += ` **Producto:** ${producto}\n`;
        
        if (specs) {
            mensaje += ` **Especificaciones:** ${specs}\n`;
        }
        
        mensaje += `\nPodran enviarme ms informacin y cotizacin?\n\nGracias!`;
        
        return mensaje;
    }

    // ==================
    // CONTROL DE VISIBILIDAD
    // ==================
    show() {
        if (this.isVisible) return;
        
        this.flotantesContainer.style.opacity = '1';
        this.flotantesContainer.style.visibility = 'visible';
        this.flotantesContainer.style.transform = this.getTransformForDevice();
        
        this.isVisible = true;
    }

    hide() {
        if (!this.isVisible) return;
        
        this.flotantesContainer.style.opacity = '0';
        this.flotantesContainer.style.visibility = 'hidden';
        this.flotantesContainer.style.transform = this.getHiddenTransformForDevice();
        
        this.isVisible = false;
    }

    getTransformForDevice() {
        return BombasAyalaUtils.isMobile() 
            ? 'translateY(0) translateX(0)' 
            : 'translateY(-50%) translateX(0)';
    }

    getHiddenTransformForDevice() {
        return BombasAyalaUtils.isMobile() 
            ? 'translateY(0) translateX(100px)' 
            : 'translateY(-50%) translateX(100px)';
    }

    // ==================
    // RESPONSIVE HANDLING
    // ==================
    adjustForMobile() {
        if (BombasAyalaUtils.isMobile()) {
            this.flotantesContainer.style.cssText += `
                position: fixed;
                bottom: 20px;
                right: 20px;
                top: auto;
                transform: none;
                flex-direction: row;
                gap: 10px;
            `;
        }
        
        // Escuchar cambios de tamao
        window.addEventListener('resize', BombasAyalaUtils.debounce(() => {
            this.adjustForMobile();
        }, 250));
    }

    // ==================
    // API PBLICA
    // ==================
    forceShow() {
        this.show();
    }

    forceHide() {
        this.hide();
    }

    toggle() {
        if (this.isVisible) {
            this.hide();
        } else {
            this.show();
        }
    }

    getState() {
        return {
            isVisible: this.isVisible,
            isMobile: BombasAyalaUtils.isMobile(),
            totalFlotantes: this.flotantes.length
        };
    }
}

// ==================
// FUNCIONES ADICIONALES
// ==================

// Inicializar cotizaciones via WhatsApp
function inicializarCotizaciones() {
    const botonesCotizar = document.querySelectorAll('.boton-cotizar, .boton-producto-cotizar');
    
    botonesCotizar.forEach(boton => {
        boton.addEventListener('click', function() {
            const producto = this.dataset.producto || 'Producto no especificado';
            const specs = this.dataset.especificaciones || '';
            const telefono = this.dataset.whatsapp || '51997984102';
            
            const datos = {
                producto: producto,
                descripcion: specs
            };
            
            const mensaje = BombasAyalaUtils.createQuoteMessage(datos);
            BombasAyalaUtils.openWhatsApp(mensaje, telefono);
            
            // Tracking
            BombasAyalaUtils.trackEvent('cotizacion_click', 'productos', producto);
        });
    });
}

// ==================
// INICIALIZACIN
// ==================
function initFlotantes() {
    window.flotantesController = new FlotantesController();
    
    // Inicializar cotizaciones
    inicializarCotizaciones();
    
    // API global
    window.Flotantes = {
        show: () => window.flotantesController?.forceShow(),
        hide: () => window.flotantesController?.forceHide(),
        toggle: () => window.flotantesController?.toggle(),
        getState: () => window.flotantesController?.getState()
    };
    
    // API WhatsApp global
    window.WhatsApp = {
        open: (mensaje, telefono) => BombasAyalaUtils.openWhatsApp(mensaje, telefono),
        quote: (datos) => {
            const mensaje = BombasAyalaUtils.createQuoteMessage(datos);
            BombasAyalaUtils.openWhatsApp(mensaje);
        }
    };
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', initFlotantes);

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = FlotantesController;
}