/**
 * ========================================
 * INICIO.JS - BOMBAS AYALA
 * Hero dinmico y funcionalidades de pgina principal
 * ========================================
 */

class InicioController {
    constructor() {
        // Elementos del hero
        this.heroBackground = document.getElementById('heroBackground');
        this.heroImage = document.getElementById('heroImage');
        this.loadingIndicator = document.getElementById('loadingIndicator');
        this.heroSection = document.getElementById('hero');
        
        // Arrays de imgenes
        this.backgroundImages = [
            'build/img/descargados/inicio/foto_1_1980.webp',
            'build/img/descargados/inicio/foto_2_1980.webp',
            'build/img/descargados/inicio/foto_3_1980.webp',
            'build/img/descargados/inicio/foto_4_1980.webp'
        ];
        
        this.mainImages = [
            'build/img/descargados/inicio/foto_1_500.webp',
            'build/img/descargados/inicio/foto_2_500.webp',
            'build/img/descargados/inicio/foto_3_500.webp',
            'build/img/descargados/inicio/foto_4_500.webp'
        ];
        
        // Estado
        this.currentImageIndex = 0;
        this.intervalId = null;
        this.isVisible = true;
        
        this.init();
    }

    init() {
        if (!this.heroBackground || !this.heroImage) {
            console.warn('Hero: Elementos principales no encontrados');
            return;
        }

        this.preloadImages();
        this.initializeHero();
        this.initScrollAnimations();
        this.setupVisibilityControl();
        
        console.log(' Inicio Controller inicializado');
    }

    // ==================
    // HERO DINMICO
    // ==================
    initializeHero() {
        // Establecer primera imagen
        this.setBackgroundImage(0);
        this.setMainImage(0);
        
        // Ocultar indicador de carga
        setTimeout(() => {
            if (this.loadingIndicator) {
                this.loadingIndicator.classList.add('hidden');
            }
        }, 1500);

        // Iniciar rotacin automtica
        this.startImageRotation();
    }

    setBackgroundImage(index) {
        if (this.heroBackground) {
            this.heroBackground.style.backgroundImage = `url('${this.backgroundImages[index]}')`;
        }
    }

    setMainImage(index) {
        if (this.heroImage) {
            this.heroImage.src = this.mainImages[index];
        }
    }

    changeToNextImage() {
        this.currentImageIndex = (this.currentImageIndex + 1) % this.backgroundImages.length;
        
        this.setBackgroundImage(this.currentImageIndex);
        this.setMainImage(this.currentImageIndex);
        
        console.log(` Imagen cambiada a: ${this.currentImageIndex + 1}`);
    }

    startImageRotation() {
        // Cambiar imgenes cada 5 segundos
        this.intervalId = setInterval(() => {
            if (this.isVisible) {
                this.changeToNextImage();
            }
        }, 5000);
    }

    stopImageRotation() {
        if (this.intervalId) {
            clearInterval(this.intervalId);
            this.intervalId = null;
        }
    }

    // ==================
    // ANIMACIONES SCROLL
    // ==================
    initScrollAnimations() {
        // Animar elementos del hero
        BombasAyalaUtils.animateOnScroll('.hero-content, .hero-image');
        
        // Animaciones adicionales para otros elementos
        BombasAyalaUtils.animateOnScroll('.estadistica-item, .servicio-destacado, .testimonio-card');
        
        // Contadores animados
        this.initCounters();
    }

    initCounters() {
        const contadores = document.querySelectorAll('.numero-estadistica');
        
        if (contadores.length === 0) return;
        
        const observerContadores = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    const elemento = entry.target;
                    const objetivo = parseInt(elemento.getAttribute('data-numero'));
                    
                    if (objetivo) {
                        BombasAyalaUtils.animateCounter(elemento, 0, objetivo, 2000);
                        observerContadores.unobserve(elemento);
                    }
                }
            });
        }, { threshold: 0.5 });
        
        contadores.forEach(contador => {
            observerContadores.observe(contador);
        });
    }

    // ==================
    // CONTROL DE VISIBILIDAD
    // ==================
    setupVisibilityControl() {
        // Pausar animaciones cuando la pgina no est visible
        document.addEventListener('visibilitychange', () => {
            if (document.hidden) {
                this.pauseAnimations();
            } else {
                this.resumeAnimations();
            }
        });

        // Controlar animaciones segn el scroll
        let lastScrollTop = 0;
        window.addEventListener('scroll', BombasAyalaUtils.debounce(() => {
            const scrollTop = window.pageYOffset;
            const heroRect = this.heroSection?.getBoundingClientRect();
            
            // Si el hero no est visible, pausar rotacin
            if (heroRect && (heroRect.bottom < 0 || heroRect.top > window.innerHeight)) {
                this.isVisible = false;
            } else {
                this.isVisible = true;
            }
            
            lastScrollTop = scrollTop;
        }, 100), { passive: true });
    }

    pauseAnimations() {
        this.isVisible = false;
        if (this.heroSection) {
            this.heroSection.style.animationPlayState = 'paused';
        }
        console.log(' Animaciones pausadas');
    }

    resumeAnimations() {
        this.isVisible = true;
        if (this.heroSection) {
            this.heroSection.style.animationPlayState = 'running';
        }
        console.log(' Animaciones reanudadas');
    }

    // ==================
    // PRELOAD DE IMGENES
    // ==================
    preloadImages() {
        const allImages = [...this.backgroundImages, ...this.mainImages];
        BombasAyalaUtils.preloadImages(allImages);
        
        console.log(` Precargando ${allImages.length} imgenes del hero`);
    }

    // ==================
    // API PBLICA
    // ==================
    nextImage() {
        this.changeToNextImage();
    }

    previousImage() {
        this.currentImageIndex = this.currentImageIndex - 1;
        if (this.currentImageIndex < 0) {
            this.currentImageIndex = this.backgroundImages.length - 1;
        }
        
        this.setBackgroundImage(this.currentImageIndex);
        this.setMainImage(this.currentImageIndex);
    }

    goToImage(index) {
        if (index >= 0 && index < this.backgroundImages.length) {
            this.currentImageIndex = index;
            this.setBackgroundImage(this.currentImageIndex);
            this.setMainImage(this.currentImageIndex);
        }
    }

    getState() {
        return {
            currentImage: this.currentImageIndex,
            totalImages: this.backgroundImages.length,
            isVisible: this.isVisible,
            isRotating: this.intervalId !== null
        };
    }

    destroy() {
        this.stopImageRotation();
        console.log(' Inicio Controller destruido');
    }
}

// ==================
// FUNCIONES LEGACY (compatibilidad)
// ==================

function changeBackgroundImage() {
    if (window.inicioController) {
        window.inicioController.nextImage();
    }
}

function initializeHero() {
    // Esta funcin se mantiene para compatibilidad
    console.log(' initializeHero legacy llamada');
}

function initializeSmoothScroll() {
    // Ya manejado por utils.js
    console.log(' initializeSmoothScroll legacy llamada');
}

function initializeScrollAnimations() {
    // Ya manejado por el controller principal
    console.log(' initializeScrollAnimations legacy llamada');
}

function preloadImages() {
    // Ya manejado por el controller principal
    console.log(' preloadImages legacy llamada');
}

// ==================
// INICIALIZACIN
// ==================
function initInicio() {
    // Solo inicializar en pgina de inicio
    if (document.querySelector('#heroBackground, .hero-section')) {
        window.inicioController = new InicioController();
        
        // API global
        window.Hero = {
            next: () => window.inicioController?.nextImage(),
            previous: () => window.inicioController?.previousImage(),
            goTo: (index) => window.inicioController?.goToImage(index),
            getState: () => window.inicioController?.getState(),
            pause: () => window.inicioController?.pauseAnimations(),
            resume: () => window.inicioController?.resumeAnimations()
        };
    }
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', () => {
    initInicio();
    
    // Llamar funciones legacy para compatibilidad
    preloadImages();
    initializeHero();
    initializeSmoothScroll();
    initializeScrollAnimations();
});

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = InicioController;
}