/**
 * ========================================
 * HEADER.JS - BOMBAS AYALA
 * Header unificado responsivo y men mvil
 * ========================================
 */

class HeaderController {
    constructor() {
        this.headerUnificado = document.querySelector('.header-unificado');
        this.contactBar = document.querySelector('.contact-bar');
        this.navPrincipal = document.querySelector('.nav-principal');
        this.menuToggle = document.querySelector('.mobile-menu-toggle');
        this.menuOverlay = document.querySelector('.mobile-menu-overlay');
        this.navMenu = document.querySelector('.nav-menu');
        
        // Estados
        this.isContactBarVisible = true;
        this.lastScrollTop = 0;
        this.scrollThreshold = 80;
        this.isMobileMenuOpen = false;
        
        // Dimensiones
        this.contactBarHeight = 0;
        this.navHeight = 0;
        this.totalHeaderHeight = 0;
        
        this.init();
    }

    init() {
        if (!this.headerUnificado || !this.contactBar || !this.navPrincipal) {
            console.warn('Header: Elementos principales no encontrados');
            return;
        }

        this.calculateDimensions();
        this.setInitialBodyMargin();
        this.setupScrollListener();
        this.setupMobileMenu();
        this.handleResize();
        
        console.log(' Header Controller inicializado');
    }

    // ==================
    // CLCULO DE DIMENSIONES
    // ==================
    calculateDimensions() {
        this.contactBarHeight = this.contactBar.offsetHeight;
        this.navHeight = this.navPrincipal.offsetHeight;
        this.totalHeaderHeight = this.contactBarHeight + this.navHeight;
        
        console.log(' Dimensiones calculadas:', {
            contactBar: this.contactBarHeight,
            nav: this.navHeight,
            total: this.totalHeaderHeight
        });
    }

    setInitialBodyMargin() {
        if (window.innerWidth <= 767) {
            document.body.style.marginTop = '100px';
        } else {
            document.body.style.marginTop = '120px';
        }
    }

    updateBodyMargin() {
        if (this.isContactBarVisible) {
            if (window.innerWidth <= 767) {
                document.body.style.marginTop = '100px';
            } else {
                document.body.style.marginTop = '120px';
            }
        } else {
            if (window.innerWidth <= 767) {
                document.body.style.marginTop = '70px';
            } else {
                document.body.style.marginTop = '80px';
            }
        }
    }

    // ==================
    // SCROLL BEHAVIOR
    // ==================
    setupScrollListener() {
        let ticking = false;

        const handleScroll = () => {
            if (!ticking) {
                requestAnimationFrame(() => {
                    this.handleScrollEvent();
                    ticking = false;
                });
                ticking = true;
            }
        };

        window.addEventListener('scroll', handleScroll, { passive: true });
    }

    handleScrollEvent() {
        const currentScrollTop = window.pageYOffset || document.documentElement.scrollTop;
        const scrollDirection = currentScrollTop > this.lastScrollTop ? 'down' : 'up';
        const scrollDistance = Math.abs(currentScrollTop - this.lastScrollTop);

        // Evitar cambios por scrolls mnimos
        if (scrollDistance < 5) {
            this.lastScrollTop = currentScrollTop;
            return;
        }

        if (scrollDirection === 'down' && currentScrollTop > this.scrollThreshold) {
            this.hideContactBar();
        } else if (scrollDirection === 'up' && currentScrollTop < this.scrollThreshold) {
            this.showContactBar();
        }

        this.lastScrollTop = currentScrollTop;
    }

    hideContactBar() {
        if (!this.isContactBarVisible) return;

        this.isContactBarVisible = false;
        this.contactBar.classList.add('hidden');
        this.headerUnificado.classList.add('contact-hidden');
        document.body.classList.add('contact-hidden');
        
        this.updateBodyMargin();
        
        console.log(' Contact bar oculto');
    }

    showContactBar() {
        if (this.isContactBarVisible) return;

        this.isContactBarVisible = true;
        this.contactBar.classList.remove('hidden');
        this.headerUnificado.classList.remove('contact-hidden');
        document.body.classList.remove('contact-hidden');
        
        this.updateBodyMargin();
        
        console.log(' Contact bar mostrado');
    }

    // ==================
    // MEN MVIL
    // ==================
    setupMobileMenu() {
        if (!this.menuToggle) {
            console.warn('Botn de men mvil no encontrado');
            return;
        }

        // Toggle del men
        this.menuToggle.addEventListener('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            this.toggleMobileMenu();
        });

        // Cerrar con overlay
        if (this.menuOverlay) {
            this.menuOverlay.addEventListener('click', (e) => {
                e.preventDefault();
                e.stopPropagation();
                this.closeMobileMenu();
            });
        }

        // Cerrar al hacer clic en enlaces
        if (this.navMenu) {
            const menuLinks = this.navMenu.querySelectorAll('a');
            menuLinks.forEach(link => {
                link.addEventListener('click', () => {
                    setTimeout(() => {
                        this.closeMobileMenu();
                    }, 100);
                });
            });
        }

        // Cerrar con Escape
        document.addEventListener('keydown', (e) => {
            if (e.key === 'Escape' && this.isMobileMenuOpen) {
                this.closeMobileMenu();
            }
        });

        // Cerrar en cambio de orientacin
        window.addEventListener('orientationchange', () => {
            setTimeout(() => {
                if (this.isMobileMenuOpen) {
                    this.closeMobileMenu();
                }
            }, 100);
        });
    }

    toggleMobileMenu() {
        if (window.innerWidth > 767) return;

        if (this.isMobileMenuOpen) {
            this.closeMobileMenu();
        } else {
            this.openMobileMenu();
        }
    }

    openMobileMenu() {
        if (window.innerWidth > 767 || this.isMobileMenuOpen) return;

        this.isMobileMenuOpen = true;
        
        // Aplicar clases
        this.menuToggle?.classList.add('activo');
        this.navMenu?.classList.add('activo');
        this.menuOverlay?.classList.add('activo');
        
        // Prevenir scroll
        document.body.style.overflow = 'hidden';
        
        // Clase adicional al contenedor
        const navContainer = document.querySelector('.nav-container');
        if (navContainer) {
            navContainer.classList.add('menu-open');
        }
        
        console.log(' Men mvil abierto');
    }

    closeMobileMenu() {
        if (!this.isMobileMenuOpen) return;

        this.isMobileMenuOpen = false;
        
        // Remover clases
        this.menuToggle?.classList.remove('activo');
        this.navMenu?.classList.remove('activo');
        this.menuOverlay?.classList.remove('activo');
        
        // Restaurar scroll
        document.body.style.overflow = '';
        
        // Remover clase del contenedor
        const navContainer = document.querySelector('.nav-container');
        if (navContainer) {
            navContainer.classList.remove('menu-open');
        }
        
        console.log(' Men mvil cerrado');
    }

    // ==================
    // RESPONSIVE HANDLING
    // ==================
    handleResize() {
        window.addEventListener('resize', BombasAyalaUtils.debounce(() => {
            this.calculateDimensions();
            this.updateBodyMargin();
            
            // Cerrar men mvil si se cambia a desktop
            if (window.innerWidth > 767 && this.isMobileMenuOpen) {
                this.closeMobileMenu();
            }
            
            this.setInitialBodyMargin();
        }, 250));
    }

    // ==================
    // API PBLICA
    // ==================
    forceShowContactBar() {
        this.showContactBar();
    }

    forceHideContactBar() {
        this.hideContactBar();
    }

    getState() {
        return {
            contactBarVisible: this.isContactBarVisible,
            mobileMenuOpen: this.isMobileMenuOpen,
            scrollPosition: window.pageYOffset,
            isMobile: window.innerWidth <= 767,
            dimensions: {
                contactBar: this.contactBarHeight,
                nav: this.navHeight,
                total: this.totalHeaderHeight
            }
        };
    }

    destroy() {
        // Limpiar estilos
        document.body.style.marginTop = '';
        document.body.style.overflow = '';
        document.body.classList.remove('contact-hidden');
        
        // Cerrar men si est abierto
        if (this.isMobileMenuOpen) {
            this.closeMobileMenu();
        }
        
        console.log(' Header Controller destruido');
    }
}

// ==================
// INICIALIZACIN
// ==================
function initHeader() {
    window.headerController = new HeaderController();
    
    // API global simplificada
    window.Header = {
        show: () => window.headerController?.forceShowContactBar(),
        hide: () => window.headerController?.forceHideContactBar(),
        getState: () => window.headerController?.getState(),
        closeMenu: () => window.headerController?.closeMobileMenu()
    };
}

// Auto-inicializar
if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', initHeader);
} else {
    initHeader();
}

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = HeaderController;
}