/**
 * ========================================
 * SOPORTE.JS - BOMBAS AYALA
 * Sistema de soporte, FAQ y ayuda
 * ========================================
 */

class SoporteController {
    constructor() {
        this.faqItems = document.querySelectorAll('.faq-item');
        this.buscadorFAQ = document.getElementById('buscadorFAQ');
        this.formularioSoporte = document.getElementById('formularioSoporte');
        this.chatButton = document.querySelector('.chat-soporte-btn');
        
        // Estado
        this.faqsOriginales = [];
        this.faqActivo = null;
        
        this.init();
    }

    init() {
        this.initFAQ();
        this.initBuscador();
        this.initFormularioSoporte();
        this.initChatSoporte();
        this.initAnimaciones();
        this.guardarFAQsOriginales();
        
        console.log(' Soporte Controller inicializado');
    }

    // ==================
    // SISTEMA FAQ (ACORDEN)
    // ==================
    initFAQ() {
        if (this.faqItems.length === 0) return;

        this.faqItems.forEach(item => {
            const pregunta = item.querySelector('.faq-pregunta');
            const respuesta = item.querySelector('.faq-respuesta');
            const icono = item.querySelector('.faq-pregunta i, .faq-icono');
            
            if (!pregunta || !respuesta) return;

            // Ocultar todas las respuestas al inicio
            respuesta.style.display = 'none';
            
            // Click en pregunta
            pregunta.addEventListener('click', () => {
                this.toggleFAQ(item, respuesta, icono);
            });

            // Mejorar accesibilidad
            pregunta.setAttribute('role', 'button');
            pregunta.setAttribute('aria-expanded', 'false');
            pregunta.setAttribute('tabindex', '0');
            
            // Soporte para teclado
            pregunta.addEventListener('keydown', (e) => {
                if (e.key === 'Enter' || e.key === ' ') {
                    e.preventDefault();
                    this.toggleFAQ(item, respuesta, icono);
                }
            });
        });
    }

    toggleFAQ(item, respuesta, icono) {
        const isActive = item.classList.contains('active');
        
        // Cerrar todos los FAQs
        this.cerrarTodosFAQs();
        
        // Si no estaba activo, abrirlo
        if (!isActive) {
            this.abrirFAQ(item, respuesta, icono);
        }
    }

    abrirFAQ(item, respuesta, icono) {
        item.classList.add('active');
        respuesta.style.display = 'block';
        
        // Animar altura
        respuesta.style.maxHeight = '0';
        respuesta.style.opacity = '0';
        respuesta.style.transition = 'all 0.3s ease';
        
        setTimeout(() => {
            respuesta.style.maxHeight = respuesta.scrollHeight + 'px';
            respuesta.style.opacity = '1';
        }, 10);
        
        // Rotar icono
        if (icono) {
            icono.style.transform = 'rotate(180deg)';
        }
        
        // Actualizar accesibilidad
        const pregunta = item.querySelector('.faq-pregunta');
        if (pregunta) {
            pregunta.setAttribute('aria-expanded', 'true');
        }
        
        this.faqActivo = item;
        
        // Tracking
        const textoPregunta = pregunta?.textContent.trim() || 'FAQ sin ttulo';
        BombasAyalaUtils.trackEvent('faq_open', 'soporte', textoPregunta);
        
        console.log(' FAQ abierto:', textoPregunta);
    }

    cerrarTodosFAQs() {
        this.faqItems.forEach(otherItem => {
            const otherRespuesta = otherItem.querySelector('.faq-respuesta');
            const otherIcono = otherItem.querySelector('.faq-pregunta i, .faq-icono');
            const otherPregunta = otherItem.querySelector('.faq-pregunta');
            
            otherItem.classList.remove('active');
            
            if (otherRespuesta) {
                otherRespuesta.style.maxHeight = '0';
                otherRespuesta.style.opacity = '0';
                
                setTimeout(() => {
                    otherRespuesta.style.display = 'none';
                }, 300);
            }
            
            if (otherIcono) {
                otherIcono.style.transform = 'rotate(0deg)';
            }
            
            if (otherPregunta) {
                otherPregunta.setAttribute('aria-expanded', 'false');
            }
        });
        
        this.faqActivo = null;
    }

    // ==================
    // BUSCADOR FAQ
    // ==================
    initBuscador() {
        if (!this.buscadorFAQ) return;

        this.buscadorFAQ.addEventListener('input', BombasAyalaUtils.debounce((e) => {
            this.filtrarFAQs(e.target.value);
        }, 300));

        // Limpiar bsqueda al hacer clic en X
        const clearBtn = document.querySelector('.clear-search');
        if (clearBtn) {
            clearBtn.addEventListener('click', () => {
                this.buscadorFAQ.value = '';
                this.filtrarFAQs('');
            });
        }
    }

    filtrarFAQs(termino) {
        const terminoLower = termino.toLowerCase().trim();
        
        if (terminoLower === '') {
            // Mostrar todos los FAQs
            this.faqItems.forEach(item => {
                item.style.display = 'block';
                item.style.opacity = '1';
            });
            return;
        }

        let resultadosEncontrados = 0;

        this.faqItems.forEach(item => {
            const pregunta = item.querySelector('.faq-pregunta')?.textContent.toLowerCase() || '';
            const respuesta = item.querySelector('.faq-respuesta')?.textContent.toLowerCase() || '';
            
            const coincide = pregunta.includes(terminoLower) || respuesta.includes(terminoLower);
            
            if (coincide) {
                item.style.display = 'block';
                item.style.opacity = '1';
                resultadosEncontrados++;
            } else {
                item.style.display = 'none';
                item.style.opacity = '0';
            }
        });

        // Mostrar mensaje si no hay resultados
        this.mostrarResultadosBusqueda(resultadosEncontrados, termino);

        // Tracking
        BombasAyalaUtils.trackEvent('faq_search', 'soporte', termino, resultadosEncontrados);
        
        console.log(` Bsqueda FAQ: "${termino}" - ${resultadosEncontrados} resultados`);
    }

    mostrarResultadosBusqueda(cantidad, termino) {
        // Remover mensaje anterior
        const mensajeAnterior = document.querySelector('.mensaje-busqueda-faq');
        if (mensajeAnterior) {
            mensajeAnterior.remove();
        }

        // Si no hay resultados, mostrar mensaje
        if (cantidad === 0 && termino.trim() !== '') {
            const mensaje = document.createElement('div');
            mensaje.className = 'mensaje-busqueda-faq';
            mensaje.innerHTML = `
                <div style="text-align: center; padding: 20px; color: #666;">
                    <i class="fas fa-search" style="font-size: 2rem; margin-bottom: 10px; opacity: 0.5;"></i>
                    <p>No se encontraron preguntas que coincidan con "<strong>${termino}</strong>"</p>
                    <p style="font-size: 0.9rem;">Intenta con otros trminos o <a href="#contacto" style="color: #007bff;">contctanos directamente</a></p>
                </div>
            `;
            
            // Insertar despus del buscador
            const contenedorFAQ = this.buscadorFAQ.closest('.faq-container') || this.buscadorFAQ.parentNode;
            contenedorFAQ.appendChild(mensaje);
        }
    }

    guardarFAQsOriginales() {
        this.faqsOriginales = Array.from(this.faqItems).map(item => ({
            pregunta: item.querySelector('.faq-pregunta')?.textContent.trim(),
            respuesta: item.querySelector('.faq-respuesta')?.textContent.trim(),
            elemento: item
        }));
    }

    // ==================
    // FORMULARIO DE SOPORTE
    // ==================
    initFormularioSoporte() {
        if (!this.formularioSoporte) return;

        // Validacin en tiempo real
        const inputs = this.formularioSoporte.querySelectorAll('input, select, textarea');
        
        inputs.forEach(input => {
            input.addEventListener('blur', () => {
                BombasAyalaUtils.validateField(input);
            });

            input.addEventListener('input', () => {
                if (input.classList.contains('error')) {
                    BombasAyalaUtils.validateField(input);
                }
            });
        });

        // Submit del formulario
        this.formularioSoporte.addEventListener('submit', (e) => {
            e.preventDefault();
            this.enviarSolicitudSoporte();
        });
    }

    enviarSolicitudSoporte() {
        // Validar formulario
        if (!BombasAyalaUtils.validateForm(this.formularioSoporte)) {
            BombasAyalaUtils.showToast('Por favor, corrige los errores en el formulario.', 'error');
            return;
        }

        // Obtener datos
        const formData = new FormData(this.formularioSoporte);
        const datos = {
            nombre: formData.get('nombre'),
            email: formData.get('email'),
            telefono: formData.get('telefono'),
            tipo: formData.get('tipo') || 'consulta',
            mensaje: formData.get('mensaje'),
            prioridad: formData.get('prioridad') || 'normal'
        };

        // Mostrar estado de carga
        const botonSubmit = this.formularioSoporte.querySelector('button[type="submit"]');
        const textoOriginal = botonSubmit?.innerHTML || '';
        
        if (botonSubmit) {
            botonSubmit.innerHTML = '<i class="fas fa-spinner fa-spin"></i> Enviando...';
            botonSubmit.disabled = true;
        }

        // Crear mensaje para WhatsApp
        const mensaje = this.crearMensajeSoporte(datos);

        setTimeout(() => {
            // Abrir WhatsApp
            BombasAyalaUtils.openWhatsApp(mensaje);
            
            // Mostrar mensaje de xito
            BombasAyalaUtils.showToast('Solicitud enviada! Te contactaremos pronto.', 'success');
            
            // Limpiar formulario
            this.formularioSoporte.reset();
            const inputs = this.formularioSoporte.querySelectorAll('input, select, textarea');
            inputs.forEach(input => {
                input.classList.remove('error', 'success');
                BombasAyalaUtils.removeFieldError(input);
            });
            
            // Restaurar botn
            if (botonSubmit) {
                botonSubmit.innerHTML = textoOriginal;
                botonSubmit.disabled = false;
            }
            
            // Tracking
            BombasAyalaUtils.trackEvent('soporte_enviado', 'soporte', datos.tipo);
            
        }, 1500);
    }

    crearMensajeSoporte(datos) {
        const iconosPrioridad = {
            baja: '',
            normal: '',
            alta: '',
            urgente: ''
        };

        const iconosTipo = {
            consulta: '',
            tecnico: '',
            garantia: '',
            emergencia: '',
            otro: ''
        };

        let mensaje = `${iconosTipo[datos.tipo] || ''} **SOLICITUD DE SOPORTE**\n\n`;
        mensaje += ` **Cliente:** ${datos.nombre}\n`;
        mensaje += ` **Email:** ${datos.email}\n`;
        
        if (datos.telefono) {
            mensaje += ` **Telfono:** ${datos.telefono}\n`;
        }
        
        mensaje += ` **Tipo:** ${datos.tipo.charAt(0).toUpperCase() + datos.tipo.slice(1)}\n`;
        mensaje += `${iconosPrioridad[datos.prioridad]} **Prioridad:** ${datos.prioridad.toUpperCase()}\n\n`;
        mensaje += ` **Mensaje:**\n${datos.mensaje}\n\n`;
        mensaje += ` **Fecha:** ${new Date().toLocaleString('es-PE')}\n\n`;
        mensaje += `Por favor, responder lo antes posible. Gracias!`;

        return mensaje;
    }

    // ==================
    // CHAT DE SOPORTE
    // ==================
    initChatSoporte() {
        if (!this.chatButton) return;

        this.chatButton.addEventListener('click', () => {
            this.abrirChatSoporte();
        });

        // Auto-mostrar chat despus de cierto tiempo
        setTimeout(() => {
            this.mostrarPrompChat();
        }, 30000); // 30 segundos
    }

    abrirChatSoporte() {
        const mensaje = ` Hola! Necesito ayuda con:\n\n` +
                       `(Selecciona una opcin o escribe tu consulta)\n\n` +
                       ` Problema tcnico\n` +
                       ` Informacin de productos\n` +
                       ` Cotizacin\n` +
                       ` Agendar visita tcnica\n` +
                       ` Otra consulta`;

        BombasAyalaUtils.openWhatsApp(mensaje);

        // Tracking
        BombasAyalaUtils.trackEvent('chat_soporte_click', 'soporte', 'whatsapp');
    }

    mostrarPrompChat() {
        // Solo mostrar si no se ha interactuado mucho con la pgina
        const interacciones = this.contarInteracciones();
        
        if (interacciones < 3) {
            BombasAyalaUtils.showToast('Necesitas ayuda? Chatea con nosotros ', 'info', 5000);
        }
    }

    contarInteracciones() {
        // Contar clics, scrolls, etc. (implementacin bsica)
        return parseInt(sessionStorage.getItem('bombas_interacciones') || '0');
    }

    // ==================
    // ANIMACIONES
    // ==================
    initAnimaciones() {
        // Animar FAQs al aparecer
        BombasAyalaUtils.animateOnScroll('.faq-item');
        
        // Animacin especial para el acorden
        const observer = BombasAyalaUtils.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.opacity = '1';
                    entry.target.style.transform = 'translateY(0)';
                }
            });
        });

        this.faqItems.forEach((item, index) => {
            item.style.opacity = '0';
            item.style.transform = 'translateY(20px)';
            item.style.transition = `opacity 0.4s ease ${index * 0.1}s, transform 0.4s ease ${index * 0.1}s`;
            observer.observe(item);
        });
    }

    // ==================
    // API PBLICA
    // ==================
    abrirFAQPorTexto(textoBusqueda) {
        const faq = Array.from(this.faqItems).find(item => {
            const pregunta = item.querySelector('.faq-pregunta')?.textContent.toLowerCase();
            return pregunta?.includes(textoBusqueda.toLowerCase());
        });

        if (faq) {
            const respuesta = faq.querySelector('.faq-respuesta');
            const icono = faq.querySelector('.faq-pregunta i, .faq-icono');
            
            this.cerrarTodosFAQs();
            this.abrirFAQ(faq, respuesta, icono);
            
            // Scroll al FAQ
            faq.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
    }

    buscarFAQ(termino) {
        if (this.buscadorFAQ) {
            this.buscadorFAQ.value = termino;
            this.filtrarFAQs(termino);
        }
    }

    getStats() {
        return {
            totalFAQs: this.faqItems.length,
            faqActivo: this.faqActivo ? this.faqActivo.querySelector('.faq-pregunta')?.textContent : null,
            hasFormulario: !!this.formularioSoporte,
            hasChat: !!this.chatButton
        };
    }
}

// ==================
// INICIALIZACIN
// ==================
function initSoporte() {
    window.soporteController = new SoporteController();
    
    // API global
    window.Soporte = {
        abrirFAQ: (texto) => window.soporteController?.abrirFAQPorTexto(texto),
        buscar: (termino) => window.soporteController?.buscarFAQ(termino),
        chat: () => window.soporteController?.abrirChatSoporte(),
        stats: () => window.soporteController?.getStats()
    };
}

// Auto-inicializar
document.addEventListener('DOMContentLoaded', () => {
    initSoporte();
    
    // Incrementar contador de interacciones
    let interacciones = parseInt(sessionStorage.getItem('bombas_interacciones') || '0');
    sessionStorage.setItem('bombas_interacciones', (interacciones + 1).toString());
});

// Exportar para mdulos
if (typeof module !== 'undefined' && module.exports) {
    module.exports = SoporteController;
}