/**
 * ========================================
 * UTILS.JS - BOMBAS AYALA
 * Funciones reutilizables y utilidades comunes
 * ========================================
 */

window.BombasAyalaUtils = {
    
    // ==================
    // SMOOTH SCROLL UNIVERSAL
    // ==================
    initSmoothScroll() {
        document.querySelectorAll('a[href^="#"]').forEach(link => {
            link.addEventListener('click', function(e) {
                e.preventDefault();
                const target = document.querySelector(this.getAttribute('href'));
                if (target) {
                    target.scrollIntoView({
                        behavior: 'smooth',
                        block: 'start'
                    });
                }
            });
        });
    },

    // ==================
    // INTERSECTION OBSERVER UNIVERSAL
    // ==================
    createAnimationObserver(callback, options = {}) {
        const defaultOptions = {
            threshold: 0.1,
            rootMargin: '0px 0px -50px 0px'
        };
        return new IntersectionObserver(callback, { ...defaultOptions, ...options });
    },

    // Animacin de entrada estndar
    animateOnScroll(selector) {
        const elements = document.querySelectorAll(selector);
        
        const observer = this.createAnimationObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.style.opacity = '1';
                    entry.target.style.transform = 'translateY(0)';
                    entry.target.classList.add('animated');
                }
            });
        });

        elements.forEach(el => {
            el.style.opacity = '0';
            el.style.transform = 'translateY(30px)';
            el.style.transition = 'opacity 0.6s ease, transform 0.6s ease';
            observer.observe(el);
        });
    },

    // ==================
    // VALIDACIN DE FORMULARIOS UNIVERSAL
    // ==================
    validateField(field) {
        const value = field.value.trim();
        const isRequired = field.hasAttribute('required');
        
        // Limpiar estados previos
        field.classList.remove('error', 'success');
        this.removeFieldError(field);
        
        // Validar campo requerido
        if (isRequired && !value) {
            this.showFieldError(field, 'Este campo es obligatorio');
            return false;
        }
        
        // Validar email
        if (field.type === 'email' && value) {
            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            if (!emailRegex.test(value)) {
                this.showFieldError(field, 'Ingresa un email vlido');
                return false;
            }
        }
        
        // Validar telfono
        if (field.type === 'tel' && value) {
            const phoneRegex = /^[0-9]{9,}$/;
            if (!phoneRegex.test(value.replace(/\s+/g, ''))) {
                this.showFieldError(field, 'Ingresa un telfono vlido (mnimo 9 dgitos)');
                return false;
            }
        }
        
        // Campo vlido
        if (value) {
            field.classList.add('success');
        }
        
        return true;
    },

    showFieldError(field, message) {
        field.classList.add('error');
        
        const errorDiv = document.createElement('div');
        errorDiv.className = 'error-message';
        errorDiv.textContent = message;
        errorDiv.style.cssText = `
            color: #E74C3C;
            font-size: 0.85rem;
            margin-top: 0.25rem;
        `;
        
        field.parentNode.appendChild(errorDiv);
    },

    removeFieldError(field) {
        const errorMsg = field.parentNode.querySelector('.error-message');
        if (errorMsg) {
            errorMsg.remove();
        }
    },

    // Validar formulario completo
    validateForm(formElement) {
        const inputs = formElement.querySelectorAll('input, select, textarea');
        let isValid = true;
        
        inputs.forEach(input => {
            if (!this.validateField(input)) {
                isValid = false;
            }
        });
        
        return isValid;
    },

    // ==================
    // WHATSAPP UNIVERSAL
    // ==================
    openWhatsApp(message, phone = '51997984102') {
        const encodedMessage = encodeURIComponent(message);
        const url = `https://wa.me/${phone}?text=${encodedMessage}`;
        window.open(url, '_blank');
    },

    // Crear mensaje de cotizacin
    createQuoteMessage(data) {
        let message = ' *SOLICITUD DE COTIZACIN*\n\n';
        
        if (data.nombre) message += ` *Cliente:* ${data.nombre}\n`;
        if (data.telefono) message += ` *Telfono:* ${data.telefono}\n`;
        if (data.email) message += ` *Email:* ${data.email}\n`;
        if (data.servicio) message += ` *Servicio:* ${data.servicio}\n`;
        if (data.producto) message += ` *Producto:* ${data.producto}\n`;
        if (data.descripcion) message += ` *Descripcin:* ${data.descripcion}\n`;
        
        message += '\nEspero su cotizacin pronto!';
        
        return message;
    },

    // ==================
    // EVENT TRACKING UNIVERSAL
    // ==================
    trackEvent(action, category, label, value) {
        console.log(` Evento: ${action} | ${category} | ${label || ''}`);
        
        // Google Analytics
        if (typeof gtag !== 'undefined') {
            gtag('event', action, {
                event_category: category,
                event_label: label,
                value: value
            });
        }
    },

    // ==================
    // LAZY LOADING UNIVERSAL
    // ==================
    initLazyLoading(selector = 'img[data-src]') {
        const images = document.querySelectorAll(selector);
        
        if (images.length === 0) return;
        
        const imageObserver = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    const img = entry.target;
                    img.src = img.dataset.src;
                    img.removeAttribute('data-src');
                    img.classList.remove('lazy');
                    img.classList.add('loaded');
                    imageObserver.unobserve(img);
                }
            });
        }, {
            threshold: 0.1,
            rootMargin: '0px 0px 50px 0px'
        });
        
        images.forEach(img => {
            img.classList.add('lazy');
            imageObserver.observe(img);
        });
    },

    // ==================
    // CONTADORES ANIMADOS
    // ==================
    animateCounter(element, start, end, duration = 2000) {
        let startTime = null;
        
        function animation(currentTime) {
            if (startTime === null) startTime = currentTime;
            const timeElapsed = currentTime - startTime;
            const progress = Math.min(timeElapsed / duration, 1);
            
            const value = Math.floor(progress * (end - start) + start);
            
            // Formatear segn el tipo de nmero
            if (end.toString().includes('%')) {
                element.textContent = value + '%';
            } else if (end.toString().includes('+')) {
                element.textContent = value + '+';
            } else if (end.toString().includes('/')) {
                element.textContent = '24/7';
            } else {
                element.textContent = value.toLocaleString();
            }
            
            if (progress < 1) {
                requestAnimationFrame(animation);
            }
        }
        
        requestAnimationFrame(animation);
    },

    // ==================
    // UTILIDADES GENERALES
    // ==================
    
    // Detectar dispositivo mvil
    isMobile() {
        return window.innerWidth <= 768;
    },

    // Debounce function
    debounce(func, wait) {
        let timeout;
        return function executedFunction(...args) {
            const later = () => {
                clearTimeout(timeout);
                func(...args);
            };
            clearTimeout(timeout);
            timeout = setTimeout(later, wait);
        };
    },

    // Copiar texto al clipboard
    copyToClipboard(text) {
        if (navigator.clipboard) {
            return navigator.clipboard.writeText(text);
        } else {
            // Fallback para navegadores antiguos
            const textArea = document.createElement('textarea');
            textArea.value = text;
            document.body.appendChild(textArea);
            textArea.select();
            document.execCommand('copy');
            document.body.removeChild(textArea);
            return Promise.resolve();
        }
    },

    // Mostrar notificacin toast
    showToast(message, type = 'info', duration = 3000) {
        const toast = document.createElement('div');
        toast.className = `toast toast-${type}`;
        toast.textContent = message;
        toast.style.cssText = `
            position: fixed;
            top: 20px;
            right: 20px;
            padding: 12px 20px;
            border-radius: 8px;
            color: white;
            font-weight: 600;
            z-index: 9999;
            opacity: 0;
            transform: translateX(100%);
            transition: all 0.3s ease;
        `;
        
        // Colores segn tipo
        const colors = {
            success: '#27AE60',
            error: '#E74C3C',
            warning: '#F39C12',
            info: '#3498DB'
        };
        
        toast.style.backgroundColor = colors[type] || colors.info;
        
        document.body.appendChild(toast);
        
        // Animar entrada
        setTimeout(() => {
            toast.style.opacity = '1';
            toast.style.transform = 'translateX(0)';
        }, 100);
        
        // Animar salida
        setTimeout(() => {
            toast.style.opacity = '0';
            toast.style.transform = 'translateX(100%)';
            setTimeout(() => {
                if (document.body.contains(toast)) {
                    document.body.removeChild(toast);
                }
            }, 300);
        }, duration);
    },

    // Precargar imgenes
    preloadImages(imageUrls) {
        imageUrls.forEach(url => {
            const img = new Image();
            img.src = url;
        });
    }
};

// ==================
// AUTO-INICIALIZACIN
// ==================
document.addEventListener('DOMContentLoaded', function() {
    // Inicializar funciones comunes automticamente
    BombasAyalaUtils.initSmoothScroll();
    BombasAyalaUtils.initLazyLoading();
    
    console.log(' BombasAyala Utils inicializados');
});

// Exportar para uso en mdulos si es necesario
if (typeof module !== 'undefined' && module.exports) {
    module.exports = window.BombasAyalaUtils;
}